import { captureException, withScope } from '@sentry/browser';
import { isPresent } from '@whisklabs/typeguards';
import { Runtype } from 'runtypes';

import { api } from 'constants/config';
import { stringifyQuery } from 'helpers/query';
import { isGrpcErrorPayload } from 'helpers/typeguards';
import get from 'lodash/get';
import { Method, Query } from 'types/api';
import { ApiError } from './classes/ApiError';
import { NetworkError } from './classes/NetworkError';

interface Params<T> {
  url?: string;
  endpoint: string;
  method?: Method;
  query?: Query;
  headers?: Record<string, string>;
  validate?: Runtype<T>;
}

export const requestApi = <T>({
  url = api.url,
  endpoint = '',
  method = 'GET',
  query,
  headers = {},
  validate,
}: Params<T>): Promise<T> => {
  let fullUrl = url + endpoint;
  let body: string | undefined;

  if (method === 'GET') {
    fullUrl += stringifyQuery(query);
  } else {
    body = JSON.stringify(query);
  }

  return fetch(fullUrl, {
    body,
    headers,
    method,
    credentials: 'include',
  })
    .catch((err: Error) => {
      throw new NetworkError(err.message, { url: fullUrl, originalError: err });
    })
    .then((data: Response) => Promise.all([data.ok, data.status, data.text()]))
    .then(([ok, status, text]: [boolean, number, string]) => {
      const result = JSON.parse(text === '' ? '{}' : text);

      if (ok) {
        if (isPresent(validate)) {
          const valid = validate.validate(result);
          if (valid.success === false) {
            const error = new Error(`Validation error: ${valid.message}`);
            withScope(scope => {
              scope.setExtras({
                url: fullUrl,
                method,
                body,
                headers,
                result,
              });

              captureException(error);
            });
            throw error;
          }
        }

        return result as T;
      } else if (isGrpcErrorPayload(result)) {
        throw new ApiError(
          isPresent(result.message) ? result.message : result.code,
          {
            status,
            code: result.code,
            url: fullUrl,
          }
        );
      } else {
        const error = new Error('Wrong error response from server');
        withScope(scope => {
          scope.setExtras({
            url: fullUrl,
            method,
            body,
            headers,
            result,
          });

          captureException(error);
        });
        throw error;
      }
    });
};

interface PrepareHeadersParams {
  headers?: Record<string, string>;
  requestToken?: string;
}

const prepareGrpcHeaders = (requestToken?: string) => ({
  'Content-Type': 'application/json',
  'Cache-Control': 'no-cache, no-store, must-revalidate',
  'X-Whisk-App-Name': 'webapp',
  Pragma: 'no-cache',

  ...(isPresent(requestToken)
    ? { Authorization: `Bearer ${requestToken}` }
    : undefined),
});

const prepareAnaliticsHeaders = () =>
  isPresent(get(whisk, 'analytics.header'))
    ? { 'whisk-event': whisk.analytics.header() }
    : undefined;

export const prepareHeaders = ({
  headers,
  requestToken,
}: PrepareHeadersParams) => ({
  ...prepareAnaliticsHeaders(),
  ...prepareGrpcHeaders(requestToken),
  ...headers,
});
