import { mixpanel as mixpanelConfig } from 'constants/config';
import { trackAppLaunched as mixpanelTrackAppLaunched } from 'mixpanel/trackAppLaunched';
import { wrapper } from 'mixpanel/wrapper';
import { User } from 'types/user';

export const initMixpanel = (user?: User) => {
  wrapper.init(mixpanelConfig.token, {
    api_host: 'https://api.mixpanel.com',
    loaded: () => {
      mixpanelTrackAppLaunched();

      if (user !== undefined && !user.anonymous) {
        wrapper.identify(user.id);
      }
    },
  });
};
