import { cookie } from 'constants/config';
import { USER_TOKEN } from 'constants/cookiesKeys';

const getCookie = (key: string) =>
  new Promise<string | undefined>(resolve => {
    chrome.cookies.get(
      {
        url: cookie.domain,
        name: `${cookie.prefix}.${key}`,
      },
      value => {
        resolve(value !== null ? value.value : undefined);
      }
    );
  });

export const getUserToken = () => getCookie(USER_TOKEN);
