import { isPresent } from '@whisklabs/typeguards';
import { action, observable, when } from 'mobx';

import { collectionApi } from 'api/recipe/collectionApi';
import { trackCollectionCreated } from 'mixpanel/trackCollectionCreated';
import { Collection } from 'types/collections';

import { recipeStore } from './RecipeStore';
import { userStore } from './UserStore';

export class CollectionsStore {
  @observable isLoading = true;
  @observable collections: Collection[] = [];
  @observable selectedIds: string[] = [];

  constructor() {
    this.storeInit();
  }

  @action private setCollections = (collections: Collection[]) => {
    this.collections = collections;
    this.isLoading = false;
  };

  @action private addCollection = (collection: Collection) => {
    this.collections.unshift(collection);
    this.selectedIds.push(collection.id);
  };

  @action setSelectedIds = (collectionIds: string[]) => {
    this.selectedIds = collectionIds;
  };

  private loadCollections = async () => {
    const collections = await collectionApi.getCollections();

    this.setCollections(collections);
  };

  private storeInit = () => {
    when(
      () => isPresent(userStore.user),
      () => {
        this.loadCollections();
      }
    );
    when(
      () => isPresent(recipeStore.recipe),
      () => {
        this.setSelectedIds(recipeStore.collectionIds);
      }
    );
  };

  createCollection = async (name: string) => {
    try {
      const collection = await collectionApi.createCollection(name);

      trackCollectionCreated({ name });

      this.addCollection(collection);

      return collection;
    } catch (error) {
      throw error;
    }
  };
}

export const collectionsStore = new CollectionsStore();
