import { captureException } from '@sentry/browser';
import { app, TRACKING_ID } from 'constants/config';
import { setConfig } from 'helpers/analytics/wrapper';

export const TRACKING_CONFIG_PATH = 'global.trackingId';
export const APP_INFO_CONFIG_PATH = 'global.appInfo';
export const NAMESPACE = 'Whisk';

export const initTracking = () => {
  try {
    setConfig(TRACKING_CONFIG_PATH, TRACKING_ID);
    setConfig(APP_INFO_CONFIG_PATH, {
      name: app.name,
      build: COMMIT_HASH,
      namespace: NAMESPACE,
      version: app.version,
    });
  } catch (e) {
    captureException(e);
  }
};
