import { isPresent } from '@whisklabs/typeguards';
import i18n from 'i18next';
import { action, observable } from 'mobx';

import { GET_USER } from 'constants/messages';
import { identify } from 'helpers/analytics/wrapper';
import { sendMessage } from 'helpers/messages';
import { initMixpanel } from 'startup/initMixpanel';
import { GetUserResponse } from 'types/messages';
import { User } from 'types/user';

import { openAuth } from 'popup/messages/auth';
class UserStore {
  @observable user?: User;

  constructor() {
    this.storeInit();
  }

  @action private setUser = (user: User) => {
    this.user = user;
  };

  private async storeInit() {
    try {
      const user = await sendMessage<GetUserResponse>({ type: GET_USER });
      this.getUserSuccess(user);
    } catch (error) {
      throw error;
    }
  }

  private getUserSuccess(user: User | null) {
    initMixpanel(isPresent(this.user) ? this.user : undefined);

    if (isPresent(user) && !user.anonymous) {
      if (isPresent(user.settings.language)) {
        i18n.changeLanguage(user.settings.language);
      }

      this.setUser(user);
      identify(user.anonymous, user.id);
    } else {
      openAuth().then(window.close);
    }
  }
}

export const userStore = new UserStore();
