import { isPresent } from '@whisklabs/typeguards';
import { CollectionBE } from 'api/recipe/collection';
import { Collection } from 'types/collections';

export const adaptCollection = ({ id, name, shoppinglist }: CollectionBE = {}):
  | Collection
  | undefined =>
  isPresent(id)
    ? {
        id,
        name,
        shoppinglist: !!shoppinglist,
      }
    : undefined;
