import { RECIPE_PARSED } from 'constants/analytics';
import { track } from 'helpers/analytics/wrapper';
import { Field } from 'types/analytics';

interface Params {
  recipeUrl?: string;
  recipePublisher?: string;
  fields: Field[];
  binalModel: boolean;
}

export const trackRecipeParsed = ({
  recipeUrl,
  recipePublisher,
  fields,
  binalModel,
}: Params) => {
  track(RECIPE_PARSED, {
    recipeUrl,
    recipePublisher,
    fields,
    binalModel,
  });
};
