const DEV_ENV = 'development';
const APP_ENV =
  process.env.APP_ENV !== undefined ? process.env.APP_ENV : DEV_ENV;

interface EnvMap<T> {
  staging: T;
  [env: string]: T | undefined;
}

const getByEnv = <T>(obj: EnvMap<T>): T => {
  const value = obj[APP_ENV];
  return value !== undefined ? value : obj.staging;
};

export const ENV_IS_NOT_DEV = APP_ENV !== DEV_ENV;
export const ENV_IS_DEV = APP_ENV === DEV_ENV;

