import { observer } from 'mobx-react-lite';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { collectionsStore } from 'stores';
import { StyledBasePopup, StyledSelect } from './markup';
import { Popup } from './Popup';

export const CollectionsSelect: React.FC = observer(() => {
  const { t } = useTranslation();

  return (
    <>
      <StyledBasePopup
        placement="top-start"
        modifiers={[
          {
            name: 'offset',
            options: {
              offset: [0, 10],
            },
          },
        ]}
        renderTrigger={({ onToggle, isOpen }) => (
          <StyledSelect
            text={
              isOpen
                ? t('collectionsSelected', {
                    count: collectionsStore.selectedIds.length,
                  })
                : collectionsStore.selectedIds.length > 0
                ? t('savedInCollections', {
                    count: collectionsStore.selectedIds.length,
                  })
                : t('selectCollection')
            }
            isPlaceholder={!isOpen && collectionsStore.selectedIds.length === 0}
            isOpen={isOpen}
            onRequestOpen={onToggle}
            onRequestClose={onToggle}
          />
        )}
        renderContent={({ onClose }) => <Popup onClose={onClose} />}
      />
    </>
  );
});
