import { getFields, getUnstructuredParsingUsed } from 'helpers/recipe';
import { ExtractedRecipe } from 'types/recipes';

import { wrapper } from './wrapper';

interface Params {
  recipe: ExtractedRecipe;
}

export const trackRecipeEditClicked = ({ recipe }: Params) => {
  wrapper.track('Recipe Edit Clicked', {
    'Binal Model': getUnstructuredParsingUsed(recipe),
    Fields: getFields(recipe),
  });
};
