import iconPlus from '@whisklabs/b2c-ui-kit/build/icons/plus.svg';
import { observer } from 'mobx-react-lite';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { trackSelectCollectionsOpened } from 'mixpanel/trackSelectCollectionsOpened';
import { FullSizePreloader } from 'popup/components/blocks/FullSizePreloader';
import { collectionsStore } from 'stores';

import { useCollectionsSelectStore } from '../useCollectionsSelectStore';

import { NewCollection } from '../NewCollection';
import { Item } from './Item';
import {
  Container,
  OptionsContainer,
  PreloaderContainer,
  StyledButton,
  StyledLink,
} from './markup';

interface Props {
  onClose: () => void;
}

export const Popup: React.FC<Props> = observer(({ onClose }) => {
  const collectionsSelectStore = useCollectionsSelectStore();

  const { t } = useTranslation();

  const [isInputVisible, setIsInputVisible] = useState(false);

  useEffect(() => {
    trackSelectCollectionsOpened();
  }, []);

  useEffect(() => {
    if (collectionsSelectStore.isSavingCollections) {
      onClose();
    }
  }, [collectionsSelectStore.isSavingCollections]);

  return (
    <Container>
      {isInputVisible ? (
        <NewCollection collectionsSelectStore={collectionsSelectStore} />
      ) : (
        <StyledLink onClick={() => setIsInputVisible(true)} icon={iconPlus}>
          {t('newCollection')}
        </StyledLink>
      )}

      {collectionsStore.isLoading ? (
        <PreloaderContainer>
          <FullSizePreloader size={20} />
        </PreloaderContainer>
      ) : (
        <OptionsContainer>
          {collectionsStore.collections.map(collection => (
            <Item key={collection.id} collection={collection} />
          ))}
        </OptionsContainer>
      )}
      <StyledButton
        intent="secondary"
        onClick={collectionsSelectStore.saveCollections}
        isFill
      >
        {t('save')}
      </StyledButton>
    </Container>
  );
});
