import { isPresent } from '@whisklabs/typeguards';
import mixpanel, { Config, Dict } from 'mixpanel-browser';

import { app, ENV_IS_NOT_DEV } from 'constants/config';

const withMixpanel = <T>(cb: () => T): T | undefined => {
  if (ENV_IS_NOT_DEV || isPresent(process.env.FORCE_ANALYTICS)) {
    return cb();
  }
};

export const wrapper = new (class {
  people = {
    increment(name: string, delta = 1) {
      withMixpanel(() => mixpanel.people.increment(name, delta));
    },

    set(properties: Dict = {}) {
      withMixpanel(() => mixpanel.people.set(properties));
    },
  };

  // General methods

  init(token: string, options: Partial<Config> = {}) {
    withMixpanel(() => mixpanel.init(token, options));
  }

  reset() {
    withMixpanel(() => mixpanel.reset());
  }

  // Identity related methods

  alias(uid: string) {
    withMixpanel(() => mixpanel.alias(uid));
  }

  identify(uid: string) {
    withMixpanel(() => mixpanel.identify(uid));
  }

  // Track related methods

  track(event: string, properties: Dict = {}) {
    withMixpanel(() =>
      mixpanel.track(event, {
        'App Id': app.name,
        'App Version': app.version,
        Authenticated: true,
        ...properties,
      })
    );
  }
})();
