import { isText } from '@whisklabs/typeguards';
import some from 'lodash/some';

import { whiskyUrl } from 'constants/config';

const exceptions = ['chrome://', 'https://chrome.google.com', whiskyUrl];

export const isInvalidUrl = (url?: string): boolean =>
  !isText(url) || some(exceptions, x => url.startsWith(x));
