import i18n from 'i18next';
import ICU from 'i18next-icu';
import { initReactI18next } from 'react-i18next';

import map from 'lodash/map';
import mapValues from 'lodash/mapValues';
import merge from 'lodash/merge';

import { languageMap } from 'constants/languages';
import { Language, ShortLanguage } from 'types/common';

export const initI18n = () => {
  i18n
    .use(ICU)
    .use(initReactI18next)
    .init({
      lng: Language.EN_US,
      fallbackLng: {
        default: [Language.EN_US],
        [ShortLanguage.EN]: [Language.EN_US],
        [ShortLanguage.DE]: [Language.DE_DE],
        [ShortLanguage.KO]: [Language.KO_KR],
        [ShortLanguage.ES]: [Language.ES_ES],
        [ShortLanguage.FR]: [Language.FR_FR],
        // [ShortLanguage.HI]: [Language.HI_IN],
        [ShortLanguage.IT]: [Language.IT_IT],
        // [ShortLanguage.PT]: [Language.PT_BR],
      },
      supportedLngs: map(merge(Language, ShortLanguage)),
      resources: mapValues(languageMap, translation => ({
        translation,
      })),
      keySeparator: false,
      interpolation: {
        escapeValue: false,
      },
    });
};
