import { authUrl, whiskyUrl } from 'constants/config';

import { stringifyQuery } from './query';

const utmParameters = {
  utm_campaign: 'whisk',
  utm_medium: 'browser extension',
  utm_source: 'browser extension',
};

export const viewRecipesRoute = () =>
  `${whiskyUrl}/recipes${stringifyQuery(utmParameters)}`;

export const recipeRoute = (id: string) =>
  `${whiskyUrl}/recipes/${id}${stringifyQuery(utmParameters)}`;

export const recipeFormRoute = (url: string, image: string | undefined) => {
  const query = {
    recipe_url: url,
    recipe_opened_from: 'Browser Extension',
    cover_image: image,
    ...utmParameters,
  };

  return `${whiskyUrl}/recipes/new${stringifyQuery(query)}`;
};

export const isAuthUrlFinished = (url: string) =>
  url.includes(`${authUrl}/finished`);
