import { Input } from '@whisklabs/b2c-ui-kit';
import iconPlus from '@whisklabs/b2c-ui-kit/build/icons/plus.svg';
import { isText } from '@whisklabs/typeguards';
import { observer } from 'mobx-react-lite';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { UseCollectionsSelectStore } from '../useCollectionsSelectStore';
import { StyledIconButton } from './markup';

interface Props {
  collectionsSelectStore: UseCollectionsSelectStore;
}

export const NewCollection: React.FC<Props> = observer(
  ({ collectionsSelectStore }) => {
    const { t } = useTranslation();

    const handleSubmit = useCallback((e: React.FormEvent) => {
      e.preventDefault();

      collectionsSelectStore.createCollection();
    }, []);

    return (
      <form noValidate onSubmit={handleSubmit}>
        <Input
          loading={collectionsSelectStore.isCreatingCollection}
          withLoading
          value={collectionsSelectStore.name}
          onChange={collectionsSelectStore.changeName}
          placeholder={t('addCollection')}
          autoFocus
          autoComplete="off"
          withError
          error={
            isText(collectionsSelectStore.error)
              ? t(collectionsSelectStore.error)
              : undefined
          }
          iconComponent={<StyledIconButton type="submit" glyph={iconPlus} />}
        />
      </form>
    );
  }
);
