import { isPresent } from '@whisklabs/typeguards';

import {
  CreateCollectionResponse,
  GetCollectionsResponse,
} from 'api/recipe/collectionApi';
import { Collection } from 'types/collections';

import { adaptCollection } from './collection';

export const adaptCreateCollectionResponse = (
  data: CreateCollectionResponse
): Collection | undefined => {
  return adaptCollection(data.collection);
};

export const adaptGetCollectionsResponse = ({
  collections = [],
}: GetCollectionsResponse): Collection[] =>
  collections.map(data => adaptCollection(data.collection)).filter(isPresent);
