import { captureException } from '@sentry/browser';
import { isPresent } from '@whisklabs/typeguards';
import { Array, Partial, Static } from 'runtypes';

import {
  adaptCreateCollectionResponse,
  adaptGetCollectionsResponse,
} from 'api/adapters/recipe/collectionApi';

import { baseApi } from '../baseApi';

import { CollectionBEScheme } from './collection';

const CreateCollectionResponseScheme = Partial({
  collection: CollectionBEScheme,
});

export type CreateCollectionResponse = Static<
  typeof CreateCollectionResponseScheme
>;

const CollectionElementScheme = Partial({
  collection: CollectionBEScheme,
});

const GetCollectionsResponseScheme = Partial({
  collections: Array(CollectionElementScheme),
});

export type GetCollectionsResponse = Static<
  typeof GetCollectionsResponseScheme
>;

class CollectionApi {
  createCollection(name: string) {
    return baseApi
      .request({
        endpoint: '/v1/collections',
        method: 'POST',
        query: {
          name,
        },
        validate: CreateCollectionResponseScheme,
      })
      .then(adaptCreateCollectionResponse)
      .then(collection => {
        if (isPresent(collection)) {
          return collection;
        }

        const error = new Error('Create collection: not valid response');
        captureException(error);
        throw error;
      });
  }

  getCollections() {
    return baseApi
      .request({
        endpoint: '/v1/collections',
        validate: GetCollectionsResponseScheme,
      })
      .then(adaptGetCollectionsResponse);
  }
}

export const collectionApi = new CollectionApi();
