import { Field } from 'types/analytics';

import { wrapper } from './wrapper';

interface Params {
  recipeUrl?: string;
  recipePublisher?: string;
  imagesAvailable: number;
  fields: Field[];
  binalModel: boolean;
}

export const trackRecipeParsed = ({
  recipeUrl,
  recipePublisher,
  imagesAvailable,
  fields,
  binalModel,
}: Params) => {
  wrapper.track('Recipe Parsed', {
    'Recipe Url': recipeUrl,
    'Recipe Publisher': recipePublisher,
    'Images Available': imagesAvailable,
    Fields: fields,
    'Binal Model': binalModel,
  });
};
