import { getUnstructuredParsingUsed } from 'helpers/recipe';
import { ExtractedRecipe } from 'types/recipes';

import { wrapper } from './wrapper';

interface Params {
  recipe: ExtractedRecipe;
}

export const trackRecipeSaveClicked = ({ recipe }: Params) => {
  wrapper.track('Recipe Save Clicked', {
    'Binal Model': getUnstructuredParsingUsed(recipe),
  });
};
