import React, { useCallback } from 'react';
import { Trans, useTranslation } from 'react-i18next';

import { Icon } from '@whisklabs/b2c-ui-kit';
import iconHorizontalArrow from 'assets/icons/horizontal-arrow.svg';
import iconLogo from 'assets/icons/logo.svg';
import imageBook from 'assets/images/book.png';
import imageBook2x from 'assets/images/book@2x.png';
import { viewRecipesRoute } from 'helpers/routes';
import { trackViewMyRecipesClick } from 'mixpanel/trackViewMyRecipesClick';

import {
  ArrowIconContainer,
  Container,
  LearnLink,
  RecipesLink,
  StyledIcon,
  StyledImage,
  StyledText,
} from './markup';

const LEARN_LINK =
  'https://help.whisk.com/en/articles/3415009-why-some-recipes-don-t-save';

interface Props {
  url?: string;
}

export const NoUrl: React.FC<Props> = () => {
  const { t } = useTranslation();

  const handleViewMyRecipes = useCallback(() => {
    trackViewMyRecipesClick('Non-recipe');
  }, []);

  return (
    <Container>
      <StyledImage
        src={imageBook}
        srcSet={`${imageBook2x} 2x`}
        title="Recipies book placeholder"
        alt="Recipies book placeholder"
      />
      <StyledText large>
        <Trans
          defaults="openRecipePage"
          components={[
            <StyledIcon key={0} glyph={iconLogo} width={18} height={18} />,
          ]}
        />
      </StyledText>
      <RecipesLink
        href={viewRecipesRoute()}
        onClick={handleViewMyRecipes}
        target="_blank"
      >
        {t('viewMyRecipes')}
      </RecipesLink>
      <LearnLink
        {...{
          href: LEARN_LINK,
          rel: 'noreferrer noopenner',
          target: '_blank',
        }}
      >
        {t('learnAboutSavingRecipes')}
        <ArrowIconContainer>
          <Icon glyph={iconHorizontalArrow} width={10} height={15} />
        </ArrowIconContainer>
      </LearnLink>
    </Container>
  );
};
