import { authUrl } from 'constants/config';
import { isAuthUrlFinished } from './routes';

export const getCurrentUrl = (): Promise<string | undefined> =>
  new Promise(resolve => {
    chrome.tabs.query({ active: true, lastFocusedWindow: true }, tabs => {
      if (tabs.length === 0) return resolve();
      resolve(tabs[0].url);
    });
  });

export const openAuthWindow = () => {
  const width = 620;
  const height = 620;

  chrome.windows.create(
    {
      url: authUrl,
      type: 'popup',
      width,
      height,
      left: Math.round((window.screen.availWidth - width) / 2),
      top: Math.round((window.screen.availHeight - height) / 2),
    },
    openedWindow => {
      if (openedWindow === undefined) return;

      const tabs = openedWindow.tabs;

      if (tabs === undefined || tabs.length === 0) return;

      const tab = tabs[0];

      const updateHadler = (id: number, update: chrome.tabs.TabChangeInfo) => {
        if (tab.id !== id) return;

        if (update.url !== undefined && isAuthUrlFinished(update.url)) {
          chrome.windows.remove(openedWindow.id);

          removeListeners();
        }
      };

      const removeHandler = (id: number) => {
        if (openedWindow.id !== id) return;

        removeListeners();
      };

      const removeListeners = () => {
        chrome.tabs.onUpdated.removeListener(updateHadler);
        chrome.windows.onRemoved.removeListener(removeHandler);
      };

      chrome.tabs.onUpdated.addListener(updateHadler);
      chrome.windows.onRemoved.addListener(removeHandler);
    }
  );
};
