import { RECIPE_SAVED } from 'constants/analytics';
import { track } from 'helpers/analytics/wrapper';
import { Field } from 'types/analytics';

interface Params {
  recipeUrl?: string;
  recipePublisher?: string;
  filledFields: Field[];
}

export const trackRecipeSaved = ({
  recipeUrl,
  recipePublisher,
  filledFields,
}: Params) => {
  track(RECIPE_SAVED, {
    importType: 'Browser Extension',
    filledFields,
    recipeUrl,
    recipePublisher,
  });
};
