import { isPresent, isUndefined } from '@whisklabs/typeguards';
import { action, computed, observable, when } from 'mobx';

import { recipeApi } from 'api/recipe/recipeApi';
import {
  getRecipeCollectionIds,
  getRecipeIsSaved,
  isNotPartial,
  isSameImage,
} from 'helpers/recipe';
import { isInvalidUrl } from 'helpers/url';
import { trackCollectionsSaved } from 'mixpanel/trackCollectionsSaved';
import { ExtractedRecipe, PartialRecipeDetails } from 'types/recipes';

import { notificationsStore } from '../NotificationsStore';
import { urlStore } from '../UrlStore';
import { userStore } from '../UserStore';

import {
  createRecipePayloadWithParams,
  trackLoadRecipeFail,
  trackLoadRecipeSuccess,
  trackSaveRecipe,
} from './helpers';

import { RecipeParams } from './types';

class RecipeStore {
  @observable recipe?: ExtractedRecipe;
  @observable isLoading = true;
  @observable isSaving = false;
  @observable isMinimal = false;

  constructor() {
    this.storeInit();
  }

  @action private stopLoading = () => {
    this.isLoading = false;
  };

  @action private startSaving = () => {
    this.isSaving = true;
  };

  @action private setRecipe = (recipe?: ExtractedRecipe) => {
    this.recipe = recipe;
    this.isLoading = false;
    this.isSaving = false;
  };

  @action private setMinimalRecipe = (recipe?: PartialRecipeDetails) => {
    this.isMinimal = true;
    this.setRecipe(recipe);
  };

  @computed get images() {
    return isPresent(this.recipe) ? this.recipe.images : [];
  }

  @computed get ingredients() {
    return isPresent(this.recipe) ? this.recipe.ingredients : [];
  }

  @computed get isRecipeSaved() {
    return isPresent(this.recipe) && getRecipeIsSaved(this.recipe);
  }

  @computed get collectionIds() {
    return isPresent(this.recipe) ? getRecipeCollectionIds(this.recipe) : [];
  }

  private storeInit = () => {
    when(
      () => !urlStore.isLoading && isPresent(userStore.user),
      () => {
        if (isUndefined(urlStore.url) || isInvalidUrl(urlStore.url)) {
          this.stopLoading();
          return;
        }

        this.loadRecipe(urlStore.url);
      }
    );
  };

  private loadRecipe = async (url: string) => {
    try {
      const recipe = await recipeApi.extractRecipe(url);

      if (isUndefined(recipe)) return;

      this.setRecipe(recipe);
      trackLoadRecipeSuccess(recipe);
    } catch (e) {
      trackLoadRecipeFail(url);
      this.stopLoading();
    }
  };

  createMinimal = (name: string) => {
    const recipe: PartialRecipeDetails = {
      name,
      ingredients: [],
      instructions: [],
      durations: {},
      source: {
        url: urlStore.url,
      },
      images: [],
      unstructuredParsingUsed: false,
      contentPolicyViolation: {
        isViolated: false,
        isImageViolated: false,
        isIngredientsViolated: false,
        isTextViolated: false,
      },
    };

    this.setMinimalRecipe(recipe);
    this.saveRecipe();
  };

  saveRecipe = async (image?: string) => {
    const recipe = this.recipe;

    if (isUndefined(recipe) || isUndefined(recipe.name)) return;

    this.startSaving();

    try {
      const isImageChanged = isPresent(image) && !isSameImage(recipe, image);

      const request =
        isNotPartial(recipe) && !isImageChanged
          ? { recipeId: recipe.id, collectionIds: [] }
          : {
              collectionIds: [],
              payload: createRecipePayloadWithParams(
                {
                  ...recipe,
                  // To make ts happy for required name
                  name: recipe.name,
                },
                { image }
              ),
            };

      const savedRecipe = await recipeApi.saveRecipe(request);

      this.setRecipe(savedRecipe);
      trackSaveRecipe(savedRecipe);
    } catch (error) {
      throw error;
    }
  };

  editRecipe = async (params: RecipeParams) => {
    const recipe = this.recipe;

    if (isUndefined(recipe) || !isNotPartial(recipe)) {
      return;
    }

    try {
      const editedRecipe = await recipeApi.updateRecipe(recipe.id, {
        collectionIds: this.collectionIds,
        payload: createRecipePayloadWithParams(recipe, params),
      });

      this.setRecipe(editedRecipe);
    } catch (error) {
      throw error;
    }
  };

  saveCollections = async (collectionIds: string[]) => {
    if (isUndefined(this.recipe) || !isNotPartial(this.recipe)) {
      return;
    }

    try {
      const updatedRecipe = await recipeApi.updateRecipe(this.recipe.id, {
        collectionIds,
      });

      this.setRecipe(updatedRecipe);
      notificationsStore.show('recipeSaved');
      trackCollectionsSaved({ count: collectionIds.length });
    } catch (error) {
      throw error;
    }
  };
}

export const recipeStore = new RecipeStore();
