import { isUndefined } from '@whisklabs/typeguards';

import { trackRecipeParsed } from 'helpers/analytics/trackRecipeParsed';
import { trackRecipeSaved } from 'helpers/analytics/trackRecipeSaved';
import {
  getFields,
  getRecipeImages,
  getRecipeSourceName,
  getRecipeSourceUrl,
  getUnstructuredParsingUsed,
} from 'helpers/recipe';
import { trackRecipeParsed as mixpanelTrackRecipeParsed } from 'mixpanel/trackRecipeParsed';
import { trackRecipeSaved as mixpanelTrackRecipeSaved } from 'mixpanel/trackRecipeSaved';
import { Field } from 'types/analytics';
import {
  ExtractedRecipe,
  PartialRecipeDetails,
  RecipeDetails,
  RecipePayload,
} from 'types/recipes';

import { RecipeParams } from './types';

export const trackSaveRecipe = (recipe?: RecipeDetails) => {
  if (isUndefined(recipe)) return;

  const params = {
    recipeUrl: getRecipeSourceUrl(recipe),
    recipePublisher: getRecipeSourceName(recipe, undefined),
    filledFields: getFields(recipe),
  };

  trackRecipeSaved(params);
  mixpanelTrackRecipeSaved(params);
};

export const trackLoadRecipeSuccess = (
  recipe: RecipeDetails | PartialRecipeDetails
) => {
  const params = {
    recipeUrl: getRecipeSourceUrl(recipe),
    recipePublisher: getRecipeSourceName(recipe, undefined),
    imagesAvailable: getRecipeImages(recipe).length,
    fields: getFields(recipe),
    binalModel: getUnstructuredParsingUsed(recipe),
  };

  trackRecipeParsed(params);
  mixpanelTrackRecipeParsed(params);
};

export const trackLoadRecipeFail = (url: string) => {
  const minimalParams = {
    recipeUrl: url,
    imagesAvailable: 0,
    fields: [Field.SourceURL],
    binalModel: false,
  };

  trackRecipeParsed(minimalParams);
  mixpanelTrackRecipeParsed(minimalParams);
};

export const createRecipePayload = (
  recipe: ExtractedRecipe & { name: string }
): RecipePayload => {
  return {
    name: recipe.name,
    description: recipe.description ?? null,
    ingredients: recipe.ingredients,
    instructions: recipe.instructions,
    image: recipe.images[0] ?? null,
    source: {
      displayName: recipe.source.displayName,
      url: recipe.source.url,
    },
    servings: recipe.servings ?? null,
    durations: recipe.durations,
  };
};

export const createRecipePayloadWithParams = (
  recipe: ExtractedRecipe & { name: string },
  params: RecipeParams = {}
): RecipePayload => {
  const payload = createRecipePayload(recipe);

  return {
    ...payload,
    name: params.name ?? payload.name,
    image: params.image ?? payload.image,
  };
};
