import {
  Array,
  Boolean,
  Literal,
  Number,
  Partial,
  Static,
  String,
  Union,
} from 'runtypes';

import { ImageContainerBEScheme, OriginalImageBE } from '../shared/image';

export const RecipeIngredientRawBEScheme = Partial({
  text: String,
  group: String,
});

export type RecipeIngredientRawBE = Static<typeof RecipeIngredientRawBEScheme>;

export const RecipeInstructionBEScheme = Partial({
  text: String,
  group: String,
});

export type RecipeInstructionBE = Static<typeof RecipeInstructionBEScheme>;

export const RecipeInstructionsBEScheme = Partial({
  steps: Array(RecipeInstructionBEScheme),
});

export type RecipeInstructionsBE = Static<typeof RecipeInstructionsBEScheme>;

export const RecipeSourceBEScheme = Partial({
  name: String,
  display_name: String,
  source_recipe_url: String,
  image: ImageContainerBEScheme,
});

export type RecipeSourceBE = Static<typeof RecipeSourceBEScheme>;

export const RecipeDurationsBEScheme = Partial({
  cook_time: Number,
  prep_time: Number,
});

export type RecipeDurationsBE = Static<typeof RecipeDurationsBEScheme>;

export const RecipeUserBEScheme = Partial({
  collections: Array(
    Partial({
      id: String,
    })
  ),
  value: Boolean,
});

export type RecipeUserBE = Static<typeof RecipeUserBEScheme>;

export interface ManualRecipeSourceBE {
  display_name?: string;
  source_recipe_url?: string;
}

export const MODERATION_STATUS_INVALID = 'MODERATION_STATUS_INVALID';
export const MODERATION_STATUS_SUCCESS = 'MODERATION_STATUS_SUCCESS';
export const MODERATION_STATUS_FAILURE = 'MODERATION_STATUS_FAILURE';

export const ModerationStatusBEScheme = Union(
  Literal(MODERATION_STATUS_INVALID),
  Literal(MODERATION_STATUS_SUCCESS),
  Literal(MODERATION_STATUS_FAILURE)
);

export type ModerationStatusBE = Static<typeof ModerationStatusBEScheme>;

export const ContentPolicyViolationBEScheme = Partial({
  violated: Boolean,
  text_status: ModerationStatusBEScheme,
  image_status: ModerationStatusBEScheme,
  ingredients_status: ModerationStatusBEScheme,
});

export type ContentPolicyViolationBE = Static<
  typeof ContentPolicyViolationBEScheme
>;

export const RecipeDetailsBEScheme = Partial({
  id: String,
  name: String,
  description: String,
  ingredients: Array(RecipeIngredientRawBEScheme),
  instructions: RecipeInstructionsBEScheme,
  images: Array(ImageContainerBEScheme),
  source: RecipeSourceBEScheme,
  servings: Number,
  durations: RecipeDurationsBEScheme,
  user: RecipeUserBEScheme,
  content_policy_violation: ContentPolicyViolationBEScheme,
});

export type RecipeDetailsBE = Static<typeof RecipeDetailsBEScheme>;

export const PartialRecipeDetailsBEScheme = Partial({
  name: String,
  description: String,
  ingredients: Array(RecipeIngredientRawBEScheme),
  instructions: RecipeInstructionsBEScheme,
  images: Array(ImageContainerBEScheme),
  source: RecipeSourceBEScheme,
  servings: Number,
  durations: RecipeDurationsBEScheme,
  unstructured_parsing_used: Boolean,
  content_policy_violation: ContentPolicyViolationBEScheme,
});

export type PartialRecipeDetailsBE = Static<
  typeof PartialRecipeDetailsBEScheme
>;

export interface ManualRecipePayloadBE {
  name: string;
  description?: string;
  ingredients: RecipeIngredientRawBE[];
  instructions: RecipeInstructionBE[];
  images: OriginalImageBE[];
  source: ManualRecipeSourceBE;
  servings?: number;
  durations: RecipeDurationsBE;
}
