import { Field } from 'types/analytics';

import { wrapper } from './wrapper';

interface Params {
  recipeUrl?: string;
  recipePublisher?: string;
  filledFields: Field[];
}

export const trackRecipeSaved = ({
  recipeUrl,
  recipePublisher,
  filledFields,
}: Params) => {
  wrapper.track('Recipe Saved', {
    'Recipe Url': recipeUrl,
    'Recipe Publisher': recipePublisher,
    'Filled Fields': filledFields,
    'Import Type': 'Browser Extension',
  });

  wrapper.people.increment('Number of Saved Recipes');
};
