import React, { useCallback, useState } from 'react';

import { trackUpdateClicked } from 'mixpanel/trackUpdateClicked';
import { recipeStore } from 'stores';

interface ReturnValue {
  name: string;
  isError: boolean;
  onChange: (name: string) => void;
  onUpdate: (e: React.FormEvent) => void;
  onCreateMinimal: (e: React.FormEvent) => void;
}

export const useNameForm = (): ReturnValue => {
  const { createMinimal, editRecipe } = recipeStore;
  const [name, setName] = useState('');
  const [isError, setIsError] = useState(false);

  const validate = useCallback((value: string) => {
    const isValid = value.length > 0;

    setIsError(!isValid);

    return isValid;
  }, []);

  const onCreateMinimal = useCallback(
    (e: React.FormEvent) => {
      e.preventDefault();

      if (!validate(name)) return;

      createMinimal(name);
    },
    [createMinimal, name]
  );

  const onChange = useCallback(
    (value: string) => {
      validate(value);
      setName(value);
    },
    [validate]
  );

  const onUpdate = useCallback(
    (e: React.FormEvent) => {
      e.preventDefault();

      if (!validate(name)) return;

      trackUpdateClicked();

      editRecipe({ name });
    },
    [name, validate, editRecipe]
  );

  return {
    name,
    isError,
    onChange,
    onUpdate,
    onCreateMinimal,
  };
};
