import { isUndefined } from '@whisklabs/typeguards';
import { observer } from 'mobx-react-lite';
import React from 'react';

import { isInvalidUrl } from 'helpers/url';
import { recipeStore, urlStore, userStore } from 'stores';

import { NoUrl } from './NoUrl';
import { Preloader } from './Preloader';
import { Preview } from './Preview';

export const Content: React.FC = observer(() => {
  const { user } = userStore;

  const { url, isLoading: isUrlLoading } = urlStore;
  const { isLoading: isRecipeLoading } = recipeStore;

  if (isUndefined(user) || isRecipeLoading || isUrlLoading) {
    return <Preloader />;
  }

  if (isInvalidUrl(url)) {
    return <NoUrl />;
  }

  return <Preview />;
});
