import { Icon, Link } from '@whisklabs/b2c-ui-kit';
import { isPresent, isText } from '@whisklabs/typeguards';
import { observer } from 'mobx-react-lite';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import iconCheck from 'assets/icons/check.svg';
import { getUnstructuredParsingUsed, isNotPartial } from 'helpers/recipe';
import { recipeFormRoute, recipeRoute, viewRecipesRoute } from 'helpers/routes';
import { trackRecipeEditClicked } from 'mixpanel/trackRecipeEditClicked';
import { trackRecipeSaveClicked } from 'mixpanel/trackRecipeSaveClicked';
import { trackSavedClicked } from 'mixpanel/trackSavedClicked';
import { trackViewMyRecipesClick } from 'mixpanel/trackViewMyRecipesClick';
import { recipeStore, urlStore } from 'stores';

import { ChangeImage } from './ChangeImage';
import { CollectionsSelect } from './CollectionsSelect';
import { Ingredients } from './Ingredients';
import {
  Container,
  LinkContainer,
  SavedIconContainer,
  StyledButton,
  StyledInput,
  StyledSavedButton,
  Title,
} from './markup';
import { Notifications } from './Notifications';
import { PreviewImage } from './PreviewImage';
import { previewImageStore } from './PreviewImageStore';
import { useNameForm } from './useNameForm';

export const Preview: React.FC = observer(() => {
  const { t } = useTranslation();

  const onSave = useCallback((e: React.FormEvent) => {
    e.preventDefault();
    recipeStore.saveRecipe(previewImageStore.image);
  }, []);

  const preventDefault = useCallback((e: React.FormEvent) => {
    e.preventDefault();
  }, []);

  const handleViewMyRecipes = useCallback(() => {
    trackViewMyRecipesClick('Recipe Saved');
  }, []);

  const handleEditClick = useCallback(() => {
    if (isPresent(recipeStore.recipe)) {
      trackRecipeEditClicked({
        recipe: recipeStore.recipe,
      });
    }
  }, []);

  const handleSaveClick = useCallback(() => {
    if (isPresent(recipeStore.recipe)) {
      trackRecipeSaveClicked({
        recipe: recipeStore.recipe,
      });
    }
  }, []);

  const {
    name,
    isError,
    onChange: onNameChange,
    onUpdate,
    onCreateMinimal,
  } = useNameForm();

  if (previewImageStore.isChangeImageOpened) {
    return (
      <ChangeImage
        image={previewImageStore.image}
        images={recipeStore.images}
        onChange={previewImageStore.changeImage}
        onCancel={previewImageStore.changeImageClose}
      />
    );
  }

  const hasNameButHasNoIngredients =
    isPresent(recipeStore.recipe) &&
    isText(recipeStore.recipe.name) &&
    recipeStore.ingredients.length === 0;
  const isUnstructuredParsingUsed =
    isPresent(recipeStore.recipe) &&
    getUnstructuredParsingUsed(recipeStore.recipe);
  const isPolicyViolated =
    recipeStore.recipe?.contentPolicyViolation.isViolated;

  let button;
  let onSubmit;

  if (
    isPresent(recipeStore.recipe) &&
    isNotPartial(recipeStore.recipe) &&
    recipeStore.isRecipeSaved
  ) {
    if (recipeStore.recipe.name !== name && recipeStore.isMinimal) {
      button = (
        <StyledButton loading={recipeStore.isSaving} type="submit" isFill>
          {t('update')}
        </StyledButton>
      );
      onSubmit = onUpdate;
    } else {
      button = (
        <a
          href={recipeRoute(recipeStore.recipe.id)}
          onClick={trackSavedClicked}
          target="_blank"
        >
          <StyledSavedButton isFill>
            <SavedIconContainer>
              <Icon glyph={iconCheck} width={14} height={10} />
            </SavedIconContainer>
            {t('saved')}
          </StyledSavedButton>
        </a>
      );
      onSubmit = preventDefault;
    }
  } else if (
    isUnstructuredParsingUsed ||
    hasNameButHasNoIngredients ||
    isPolicyViolated
  ) {
    button = (
      <a
        href={
          isPresent(urlStore.url)
            ? recipeFormRoute(urlStore.url, previewImageStore.image)
            : undefined
        }
        onClick={handleSaveClick}
        target="_blank"
      >
        <StyledButton isFill>{t('saveRecipe')}</StyledButton>
      </a>
    );
  } else {
    button = (
      <StyledButton
        loading={recipeStore.isSaving}
        onClick={handleSaveClick}
        type="submit"
        isFill
      >
        {t('saveRecipe')}
      </StyledButton>
    );

    onSubmit = isPresent(recipeStore.recipe) ? onSave : onCreateMinimal;
  }

  const content = (
    <>
      <PreviewImage
        image={previewImageStore.image}
        onChangeClick={
          recipeStore.images.length > 1
            ? previewImageStore.changeImageOpen
            : undefined
        }
      />
      <form onSubmit={onSubmit} noValidate>
        {isPresent(recipeStore.recipe) && !recipeStore.isMinimal ? (
          <Title>{recipeStore.recipe.name}</Title>
        ) : (
          <StyledInput
            value={name}
            placeholder={t('recipeName')}
            withError
            onChange={onNameChange}
            autoComplete="off"
            error={isError ? t('thisFieldIsRequired') : undefined}
          />
        )}
        {recipeStore.ingredients.length > 0 ? (
          <Ingredients
            ingredients={recipeStore.ingredients}
            isSaved={recipeStore.isRecipeSaved}
          />
        ) : null}
        {button}
      </form>
      {recipeStore.isRecipeSaved ? <CollectionsSelect /> : null}
      <LinkContainer>
        {recipeStore.isRecipeSaved ? (
          <Link
            href={viewRecipesRoute()}
            onClick={handleViewMyRecipes}
            target="_blank"
          >
            {t('viewMyRecipes')}
          </Link>
        ) : (
          <Link
            href={
              isPresent(urlStore.url)
                ? recipeFormRoute(urlStore.url, previewImageStore.image)
                : undefined
            }
            onClick={handleEditClick}
            target="_blank"
          >
            {t(
              recipeStore.ingredients.length === 0 ? 'addIngredients' : 'edit'
            )}
          </Link>
        )}
      </LinkContainer>
    </>
  );

  return (
    <>
      <Notifications />
      <Container>{content}</Container>
    </>
  );
});
