import React from 'react';
import { useTranslation } from 'react-i18next';

import { Icon } from '@whisklabs/b2c-ui-kit';
import iconRecipeImagePlaceholder from 'assets/icons/recipe-image-placeholder.svg';

import { ChangeButton, Container, Placeholder, StyledImage } from './markup';

interface Props {
  image?: string;
  onChangeClick?: () => void;
}

export const PreviewImage: React.FC<Props> = ({ image, onChangeClick }) => {
  const { t } = useTranslation();

  return (
    <Container>
      <StyledImage
        url={image}
        width={310}
        height={190}
        placeholder={
          <Placeholder>
            <Icon glyph={iconRecipeImagePlaceholder} width={88} height={84} />
          </Placeholder>
        }
      />
      {onChangeClick !== undefined ? (
        <ChangeButton small intent="secondary" onClick={onChangeClick}>
          {t('changeImage')}
        </ChangeButton>
      ) : null}
    </Container>
  );
};
