import { Icon } from '@whisklabs/b2c-ui-kit';
import React from 'react';

import iconArrow from 'assets/icons/arrow.svg';

import { Container, Field, IconContainer, Value } from './markup';

interface Props {
  className?: string;
  isPlaceholder?: boolean;
  isOpen: boolean;
  text: string;
  onRequestOpen: () => void;
  onRequestClose: () => void;
}

export const BaseSelect: React.FC<Props> = ({
  className,
  text,
  isOpen,
  isPlaceholder = !isOpen,
  onRequestOpen,
  onRequestClose,
}) => {
  return (
    <Container className={className}>
      <Field isOpen={isOpen} onClick={isOpen ? onRequestClose : onRequestOpen}>
        <Value isPlaceholder={isPlaceholder}>{text}</Value>
        <IconContainer isOpen={isOpen}>
          <Icon glyph={iconArrow} width={12} height={12} />
        </IconContainer>
      </Field>
    </Container>
  );
};
