import take from 'lodash/take';
import uniq from 'lodash/uniq';
import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { Item } from './Item';
import {
  ButtonsContainer,
  ImagesContainer,
  LinkContainer,
  StyledButton,
  StyledLink,
} from './markup';

const IMAGES_LIMIT = 6;

interface Props {
  image?: string;
  images: string[];
  onChange: (image: string) => void;
  onCancel: () => void;
}

export const ChangeImage: React.FC<Props> = ({
  image,
  images,
  onChange,
  onCancel,
}) => {
  const { t } = useTranslation();

  const [selectedImage, setSelectedImage] = useState(() =>
    image !== undefined ? image : images[0]
  );

  const allImages = useMemo(
    () =>
      take(
        uniq((image !== undefined ? [image] : []).concat(images)),
        IMAGES_LIMIT
      ),
    [image, images]
  );

  const handleChangeClick = useCallback(() => {
    onChange(selectedImage);
  }, [selectedImage, onChange]);

  return (
    <>
      <ImagesContainer>
        {allImages.map(x => (
          <Item
            key={x}
            image={x}
            isSelected={x === selectedImage}
            onClick={setSelectedImage}
          />
        ))}
      </ImagesContainer>
      <ButtonsContainer>
        <StyledButton onClick={handleChangeClick} isFill>
          {t('changeImage')}
        </StyledButton>
        <LinkContainer>
          <StyledLink onClick={onCancel}>{t('cancel')}</StyledLink>
        </LinkContainer>
      </ButtonsContainer>
    </>
  );
};
