import { AnalyticsEvent } from 'types/common';

// tslint:disable-next-line:no-any
export const setConfig = (path: string, value: any) => {
  whisk.queue.push(() => {
    whisk.config.set(path, value);
  });
};

// tslint:disable-next-line:no-any
export const track = (event: AnalyticsEvent, data?: any) => {
  whisk.queue.push(() => {
    whisk.analytics.track(event, data);
  });
};

export const identify = (anonymous: boolean, userId?: string) => {
  whisk.queue.push(() => {
    whisk.analytics.identify(anonymous ? undefined : userId);
  });
};
