import { isPresent, isText } from '@whisklabs/typeguards';
import { action } from 'mobx';
import { useLocalStore } from 'mobx-react-lite';

import { collectionsStore, recipeStore } from 'stores';

export const useCollectionsSelectStore = () => {
  const store = useLocalStore(() => ({
    name: '',
    error: '',
    isTouched: false,
    isCreatingCollection: false,
    isSavingCollections: false,

    setName: action((name: string) => {
      store.name = name;
    }),
    setError: action((error: string) => {
      store.error = error;
    }),
    setIsTouched: action((isTouched: boolean) => {
      store.isTouched = isTouched;
    }),

    creatingCollectionStart: action(() => {
      store.isCreatingCollection = true;
    }),
    creatingCollectionSuccess: action(() => {
      store.isCreatingCollection = false;
      store.setName('');
      store.setIsTouched(false);
    }),
    creatingCollectionFail: action(() => {
      store.isCreatingCollection = false;
      store.setError('generalError');
    }),

    saveCollectionsStart: action(() => {
      store.isSavingCollections = true;
    }),
    saveCollectionsSuccess: action(() => {
      store.isSavingCollections = false;
    }),
    saveCollectionsFail: action(() => {
      store.isSavingCollections = false;
      store.setError('generalError');
    }),

    changeName(name: string) {
      store.setName(name);

      if (store.isTouched) {
        store.validate();
      }
    },

    validate() {
      if (!isText(store.name)) {
        store.setError('thisFieldIsRequired');
        return false;
      }

      const hasSameName = isPresent(
        collectionsStore.collections.find(
          collection => collection.name === store.name
        )
      );

      if (hasSameName) {
        store.setError('collectionWithNameExists');
        return false;
      }

      store.setError('');

      return true;
    },

    async createCollection() {
      if (store.isCreatingCollection) {
        return;
      }

      store.setIsTouched(true);

      if (!store.validate()) {
        return;
      }

      store.creatingCollectionStart();

      try {
        const collection = await collectionsStore.createCollection(store.name);

        store.creatingCollectionSuccess();

        return collection;
      } catch (error) {
        store.creatingCollectionFail();
        throw error;
      }
    },

    async saveCollections() {
      if (store.isSavingCollections) {
        return;
      }

      try {
        if (isText(store.name)) {
          const collection = await store.createCollection();

          if (!isPresent(collection)) {
            return;
          }

          store.saveCollectionsStart();
          await recipeStore.saveCollections(
            collectionsStore.selectedIds.concat(collection.id)
          );
        } else {
          store.saveCollectionsStart();
          await recipeStore.saveCollections(collectionsStore.selectedIds);
        }

        store.saveCollectionsSuccess();
      } catch (error) {
        store.saveCollectionsFail();
        throw error;
      }
    },
  }));

  return store;
};

export type UseCollectionsSelectStore = ReturnType<
  typeof useCollectionsSelectStore
>;
