import { isObject, isString, isUndefined } from '@whisklabs/typeguards';

import { GrpcErrorPayload } from '../types/api';

export const isGrpcErrorPayload = (x: unknown): x is GrpcErrorPayload =>
  isObject(x) &&
  isString(x.code) &&
  (isUndefined(x.message) || isString(x.message));

export const getEnumGuard = <T extends object>(e: T) => {
  const values = Object.values(e);

  return (token: unknown): token is T[keyof T] => values.includes(token);
};
