import { analytics } from 'constants/analytics';
import { Record, Static, String, Unknown } from 'runtypes';

import { getEnumGuard } from 'helpers/typeguards';
export { Glyph } from '@whisklabs/b2c-ui-kit';

export enum Language {
  EN_US = 'en-US',
  EN_GB = 'en-GB',
  DE_DE = 'de-DE',
  KO_KR = 'ko-KR',
  ES_ES = 'es-ES',
  ES_MX = 'es-MX',
  FR_FR = 'fr-FR',
  // HI_IN = 'hi-IN',
  IT_IT = 'it-IT',
  // PT_BR = 'pt-BR',
}

const isLanguage = getEnumGuard(Language);
export const LanguageScheme = Unknown.withGuard(isLanguage);

export enum ShortLanguage {
  EN = 'en',
  DE = 'de',
  KO = 'ko',
  ES = 'es',
  FR = 'fr',
  // HI = 'hi',
  IT = 'it',
  // PT = 'pt',
}

const isShortLanguage = getEnumGuard(ShortLanguage);
export const ShortLanguageScheme = Unknown.withGuard(isShortLanguage);

export const CountryScheme = Record({
  code: String,
  displayName: String,
  phoneCode: String,
});

export type Country = Static<typeof CountryScheme>;

export type AnalyticsEvent = keyof typeof analytics;
