import { isPresent, isText, isUndefined } from '@whisklabs/typeguards';

import {
  ContentPolicyViolationBE,
  ManualRecipePayloadBE,
  ModerationStatusBE,
  MODERATION_STATUS_SUCCESS,
  PartialRecipeDetailsBE,
  RecipeDetailsBE,
  RecipeDurationsBE,
  RecipeIngredientRawBE,
  RecipeInstructionBE,
  RecipeInstructionsBE,
  RecipeSourceBE,
  RecipeUserBE,
} from 'api/recipe/recipe';
import {
  ContentPolicyViolation,
  GroupedItem,
  PartialRecipeDetails,
  RecipeDetails,
  RecipeDurations,
  RecipePayload,
  RecipeSource,
  RecipeUser,
} from 'types/recipes';

import { adaptImageContainer } from '../shared/image';

export const adaptGroupedItem = ({
  text,
  group,
}: RecipeIngredientRawBE | RecipeInstructionBE = {}): GroupedItem | undefined =>
  isPresent(text) ? { text, group } : undefined;

export const adaptRecipeInstructions = ({
  steps = [],
}: RecipeInstructionsBE = {}): GroupedItem[] =>
  steps.map(adaptGroupedItem).filter(isPresent);

export const adaptRecipeSource = ({
  name,
  display_name,
  source_recipe_url,
  image,
}: RecipeSourceBE = {}): RecipeSource => {
  return {
    name,
    displayName: display_name,
    url: source_recipe_url,
    image: adaptImageContainer(image),
  };
};

export const adaptRecipeDuration = ({
  cook_time,
  prep_time,
}: RecipeDurationsBE = {}): RecipeDurations => ({
  cookTime: cook_time,
  prepTime: prep_time,
});

export const adaptRecipeUser = (data: RecipeUserBE = {}): RecipeUser => ({
  collectionIds: data.collections?.map(x => x.id).filter(isPresent) ?? [],
  saved: !!data.value,
});

export const adaptModerationStatus = (data?: ModerationStatusBE): boolean =>
  data !== MODERATION_STATUS_SUCCESS;

export const adaptContentPolicyViolation = (
  data: ContentPolicyViolationBE = {}
): ContentPolicyViolation => ({
  isTextViolated: adaptModerationStatus(data.text_status),
  isImageViolated: adaptModerationStatus(data.image_status),
  isIngredientsViolated: adaptModerationStatus(data.ingredients_status),
  isViolated: data.violated === true,
});

export const adaptRecipeDetails = (
  data: RecipeDetailsBE = {}
): RecipeDetails | undefined =>
  isPresent(data.id) && isPresent(data.name)
    ? {
        id: data.id,
        name: data.name,
        description: data.description,
        ingredients:
          data.ingredients?.map(adaptGroupedItem).filter(isPresent) ?? [],
        instructions: adaptRecipeInstructions(data.instructions),
        images: data.images?.map(adaptImageContainer).filter(isPresent) ?? [],
        source: adaptRecipeSource(data.source),
        servings: data.servings,
        durations: adaptRecipeDuration(data.durations),
        user: adaptRecipeUser(data.user),
        contentPolicyViolation: adaptContentPolicyViolation(
          data.content_policy_violation
        ),
      }
    : undefined;

export const adaptPartialRecipeDetails = (
  data: PartialRecipeDetailsBE = {}
): PartialRecipeDetails => ({
  name: data.name,
  description: data.description,
  ingredients: data.ingredients?.map(adaptGroupedItem).filter(isPresent) ?? [],
  instructions: adaptRecipeInstructions(data.instructions),
  images: data.images?.map(adaptImageContainer).filter(isPresent) ?? [],
  source: adaptRecipeSource(data.source),
  servings: data.servings,
  durations: adaptRecipeDuration(data.durations),
  unstructuredParsingUsed: data.unstructured_parsing_used ?? false,
  contentPolicyViolation: adaptContentPolicyViolation(
    data.content_policy_violation
  ),
});

const adaptRecipePayloadGroupedItems = (
  items: GroupedItem[]
): RecipeIngredientRawBE[] | RecipeInstructionBE[] =>
  items.filter(x => isPresent(x) && isPresent(x.text) && isText(x.text.trim()));

export const adaptRecipePayload = (
  data?: RecipePayload
): ManualRecipePayloadBE | undefined => {
  if (isUndefined(data)) return;

  return {
    name: data.name,
    description: data.description ?? undefined,
    ingredients: adaptRecipePayloadGroupedItems(data.ingredients),
    instructions: adaptRecipePayloadGroupedItems(data.instructions),
    images: isPresent(data.image) ? [{ url: data.image }] : [],
    source: {
      source_recipe_url: data.source.url,
      display_name: data.source.displayName,
    },
    servings: data.servings ?? undefined,
    durations: {
      cook_time: data.durations.cookTime,
      prep_time: data.durations.prepTime,
    },
  };
};
