import { ListCheckbox } from '@whisklabs/b2c-ui-kit';
import { isPresent } from '@whisklabs/typeguards';
import without from 'lodash/without';
import { observer } from 'mobx-react-lite';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { collectionsStore } from 'stores';
import { Collection } from 'types/collections';

interface Props {
  collection: Collection;
}

export const Item: React.FC<Props> = observer(({ collection }) => {
  const { t } = useTranslation();

  const isSelected = collectionsStore.selectedIds.includes(collection.id);

  const handleChange = useCallback(() => {
    const newIds = isSelected
      ? without(collectionsStore.selectedIds, collection.id)
      : collectionsStore.selectedIds.concat(collection.id);

    collectionsStore.setSelectedIds(newIds);
  }, [isSelected]);

  return (
    <ListCheckbox
      key={collection.id}
      value={collection.id}
      checked={isSelected}
      onChange={handleChange}
    >
      {isPresent(collection.name)
        ? collection.name
        : collection.shoppinglist
        ? t('addedToSL')
        : ''}
    </ListCheckbox>
  );
});
