import { isPresent } from '@whisklabs/typeguards';

import { ExtractRecipeResponse, GetRecipeResponse } from 'api/recipe/recipeApi';
import { ExtractedRecipe, RecipeDetails } from 'types/recipes';

import { adaptPartialRecipeDetails, adaptRecipeDetails } from './recipe';

export const adaptSingleRecipeResponse = (
  data: GetRecipeResponse
): RecipeDetails | undefined => adaptRecipeDetails(data.recipe);

export const adaptExtractRecipeResponse = (
  data: ExtractRecipeResponse
): ExtractedRecipe => {
  const recipe = adaptRecipeDetails(data.recipe);

  if (isPresent(recipe)) return recipe;

  return adaptPartialRecipeDetails(data.partially_parsed);
};
