import { action, observable } from 'mobx';

import { getCurrentUrl } from 'helpers/tabs';

class UrlStore {
  @observable isLoading = true;
  @observable url?: string;

  constructor() {
    this.storeInit();
  }

  @action private setUrl = (url?: string) => {
    this.isLoading = false;
    this.url = url;
  };

  private async storeInit() {
    try {
      const url = await getCurrentUrl();
      this.setUrl(url);
    } catch (error) {
      throw error;
    }
  }
}

export const urlStore = new UrlStore();
