import { isObject, isPresent, isUndefined } from '@whisklabs/typeguards';
import omitBy from 'lodash/omitBy';
import { Runtype } from 'runtypes';

import {
  ERROR_AUTH_TOKEN_EXPIRED,
  ERROR_AUTH_TOKEN_NOT_FOUND,
  ERROR_AUTH_TOKEN_REQUIRED,
} from 'constants/grpcErrors';
import { ApiError } from 'helpers/classes/ApiError';
import { getUserToken } from 'helpers/cookies';
import { prepareHeaders, requestApi } from 'helpers/requestApi';
import { Method, Query } from 'types/api';

export const getUpdateMask = (obj: object, isRecursive = false) => {
  const keys = Object.keys(omitBy(obj, isUndefined));

  if (!isRecursive) {
    return keys;
  }

  let fieldNames: string[] = [];

  keys.forEach(key => {
    // tslint:disable-next-line:no-any
    const value = (obj as any)[key] as any;

    if (isObject(value) && !Array.isArray(value)) {
      const innerKeys = getUpdateMask(value as object);
      fieldNames = fieldNames.concat(
        innerKeys.map(innerKey => `${key}.${innerKey}`)
      );
      return;
    }
    fieldNames.push(key);
  });

  return fieldNames;
};

interface RequestParams<T> {
  endpoint: string;
  headers?: Record<string, string>;
  method?: Method;
  query?: Query;
  url?: string;
  validate?: Runtype<T>;
}

class BaseApi {
  async request<T>({
    endpoint,
    headers,
    method,
    query,
    url,
    validate,
  }: RequestParams<T>): Promise<T> {
    const requestToken = await getUserToken();

    const completeHeaders = prepareHeaders({
      headers,
      requestToken,
    });

    const onError = async (reject: (err: Error) => void, error: Error) => {
      const token = await getUserToken();

      if (isPresent(requestToken) && token !== requestToken) {
        return;
      }

      if (!(error instanceof ApiError)) {
        return reject(error);
      }

      const shouldLogout =
        error.code === ERROR_AUTH_TOKEN_NOT_FOUND ||
        error.code === ERROR_AUTH_TOKEN_REQUIRED;

      const shouldRefresh = error.code === ERROR_AUTH_TOKEN_EXPIRED;

      if (!shouldLogout && !shouldRefresh) {
        return reject(error);
      }

      window.close();
    };

    const onSuccess = async (resolve: (payload: T) => void, payload: T) => {
      const token = await getUserToken();

      if (isPresent(requestToken) && token !== requestToken) {
        return;
      }

      resolve(payload);
    };

    return new Promise<T>((resolve, reject) => {
      return requestApi<T>({
        url,
        endpoint,
        method,
        query,
        headers: completeHeaders,
        validate,
      })
        .then(data => {
          onSuccess(resolve, data);
        })
        .catch((err: Error) => {
          onError(reject, err);
        });
    });
  }

  getBasicUpdateMask(obj: object) {
    return {
      paths: getUpdateMask(obj),
    };
  }

  getBasicUpdateMaskRecursive(obj: object) {
    return {
      paths: getUpdateMask(obj, true),
    };
  }
}

export const baseApi = new BaseApi();
