import { captureException } from '@sentry/browser';
import { Partial, Static } from 'runtypes';

import { isPresent } from '@whisklabs/typeguards';
import { adaptRecipePayload } from 'api/adapters/recipe/recipe';
import {
  adaptExtractRecipeResponse,
  adaptSingleRecipeResponse,
} from 'api/adapters/recipe/recipeApi';
import { SaveRecipeRequest, UpdateRecipeRequest } from 'types/recipes';

import { baseApi } from '../baseApi';

import { PartialRecipeDetailsBEScheme, RecipeDetailsBEScheme } from './recipe';

const RECIPES_MASK = [
  'normalized_ingredients',
  'instructions',
  'nutrition',
  'content_policy_violation',
];

const GetRecipeResponseScheme = Partial({
  recipe: RecipeDetailsBEScheme,
});

export type GetRecipeResponse = Static<typeof GetRecipeResponseScheme>;

const ExtractRecipeResponseScheme = Partial({
  recipe: RecipeDetailsBEScheme,
  partially_parsed: PartialRecipeDetailsBEScheme,
});

export type ExtractRecipeResponse = Static<typeof ExtractRecipeResponseScheme>;

const SaveRecipeResponseScheme = Partial({
  recipe: RecipeDetailsBEScheme,
});

export type SaveRecipeResponse = Static<typeof SaveRecipeResponseScheme>;

const UpdateRecipeResponseScheme = Partial({
  recipe: RecipeDetailsBEScheme,
});

export type UpdateRecipeResponse = Static<typeof UpdateRecipeResponseScheme>;

class RecipeApi {
  extractRecipe(url: string) {
    return baseApi
      .request({
        endpoint: `/v1/recipes/extract`,
        method: 'POST',
        query: {
          url,
          recipe_mask: {
            paths: RECIPES_MASK,
          },
          with_unstructured_parsing: true,
        },
        validate: ExtractRecipeResponseScheme,
      })
      .then(adaptExtractRecipeResponse);
  }

  saveRecipe(data: SaveRecipeRequest) {
    return baseApi
      .request({
        endpoint: `/v1/recipes`,
        method: 'POST',
        query: {
          recipe_id: data.recipeId,
          source_user_id: 'userId' in data ? data.userId : undefined,
          collection_ids: data.collectionIds,
          payload:
            'payload' in data ? adaptRecipePayload(data.payload) : undefined,
          recipe_mask: {
            paths: RECIPES_MASK,
          },
        },
        validate: SaveRecipeResponseScheme,
      })
      .then(adaptSingleRecipeResponse)
      .then(recipe => {
        if (isPresent(recipe)) {
          return recipe;
        }

        const error = new Error('Save recipe: not valid response');
        captureException(error);
        throw error;
      });
  }

  updateRecipe(id: string, data: UpdateRecipeRequest) {
    const query = {
      payload: adaptRecipePayload(data.payload),
      collection_ids: data.collectionIds,
    };

    return baseApi
      .request({
        endpoint: `/v1/recipes/${id}`,
        method: 'PATCH',
        query: {
          ...query,
          recipe_mask: {
            paths: RECIPES_MASK,
          },
          update_mask: baseApi.getBasicUpdateMask(query),
        },
        validate: UpdateRecipeResponseScheme,
      })
      .then(adaptSingleRecipeResponse)
      .then(recipe => {
        if (isPresent(recipe)) {
          return recipe;
        }

        const error = new Error('Update recipe: not valid response');
        captureException(error);
        throw error;
      });
  }
}

export const recipeApi = new RecipeApi();
