import React, { useCallback, useState } from 'react';
import { Omit } from 'utility-types';

import { getImage, Options } from 'helpers/images';

interface Props {
  className?: string;
  url?: string;
  width: number;
  height?: number;
  options?: Omit<Options, 'width' | 'height'>;
  placeholder?: React.ReactNode;
  onClick?: () => void;
}

export const Image: React.FC<Props> = ({
  className,
  url,
  width,
  height = width,
  options,
  placeholder = null,
  onClick,
}) => {
  const [isError, setIsError] = useState(false);

  const onError = useCallback(() => setIsError(true), []);

  const fullOptions = { width, height, ...options };

  if (url === undefined || isError) return <>{placeholder}</>;

  return (
    <img
      className={className}
      src={getImage(url, fullOptions)}
      srcSet={`${getImage(url, { ...fullOptions, x2: true })} 2x`}
      onError={onError}
      onClick={onClick}
      width={width}
      height={height}
    />
  );
};
