import { cloudinary } from 'constants/config';

export interface Options {
  height?: number;
  width?: number;
  color?: string;
  x2?: boolean;
  fit?: boolean;
  additionalQuery?: Record<string, string>;
}

export const isCloudinary = (url: string) => /cloudinary/i.test(url);

export const isGoogle = (url: string) => /google/i.test(url);

const URI_RX = new RegExp('/image/upload/(.+)$');

const prefix = `https://${cloudinary.cloudName}-res.cloudinary.com/image/upload`;

const transfromUrl = (url: string, transformation: string) => {
  const regExpArray = URI_RX.exec(url);

  return regExpArray !== null
    ? `${prefix}/${transformation}/${regExpArray[1]}`
    : url;
};

const addQuery = (url: string, query?: Record<string, string>) => {
  if (query === undefined) return url;

  const searchParams = new URLSearchParams(query);

  return `${url}?${searchParams}`;
};

export const getImage = (url: string, options: Options) => {
  if (isCloudinary(url)) {
    const transformation = ['c_fill'];

    if (options.height !== undefined) {
      transformation.push(`h_${options.height}`);
    }

    if (options.width !== undefined) {
      transformation.push(`w_${options.width}`);
    }

    if (options.fit) {
      transformation.push('c_fit');
    }

    if (options.color !== undefined) {
      const color = options.color.replace('#', '');
      transformation.push(`e_colorize,co_rgb:${color}`);
    }

    if (options.x2) {
      transformation.push('dpr_2.0');
    }

    return addQuery(
      transfromUrl(url, transformation.join(',')),
      options.additionalQuery
    );
  }

  if (isGoogle(url)) {
    const params = ['c'];

    if (options.width !== undefined) {
      params.push(`w${options.x2 ? options.width * 2 : options.width}`);
    }

    if (options.height !== undefined) {
      params.push(`h${options.x2 ? options.height * 2 : options.height}`);
    }

    return addQuery(url + `=${params.join('-')}`, options.additionalQuery);
  }

  return addQuery(url, options.additionalQuery);
};
