import { Number, Partial, Static, String } from 'runtypes';

export const ResponsiveImageBEScheme = Partial({
  url: String,
  width: Number,
  height: Number,
});

export type ResponsiveImageBE = Static<typeof ResponsiveImageBEScheme>;

export const OriginalImageBEScheme = Partial({
  url: String,
});

export type OriginalImageBE = Static<typeof OriginalImageBEScheme>;

export const ImageContainerBEScheme = Partial({
  original: OriginalImageBEScheme,
  responsive: ResponsiveImageBEScheme,
});

export type ImageContainerBE = Static<typeof ImageContainerBEScheme>;
