import { isPresent } from '@whisklabs/typeguards';
import { Field } from 'types/analytics';
import {
  ExtractedRecipe,
  PartialRecipeDetails,
  RecipeDetails,
} from 'types/recipes';

export const getRecipeImageUrl = (
  recipe: ExtractedRecipe
): string | undefined => {
  return recipe.images[0];
};

export const getRecipeImages = (recipe: ExtractedRecipe): string[] =>
  recipe.images;

export const getRecipeCollectionIds = (recipe: ExtractedRecipe): string[] =>
  isNotPartial(recipe) ? recipe.user.collectionIds : [];

export const getRecipeIsSaved = (recipe: ExtractedRecipe) =>
  isNotPartial(recipe) && recipe.user.saved;

export const getRecipeSourceUrl = (
  recipe: ExtractedRecipe
): string | undefined => recipe.source?.url;

export const getRecipeSourceName = <T>(
  recipe: ExtractedRecipe,
  fallback: T
): string | T => recipe.source.name ?? fallback;

export const isNotPartial = (
  recipe: ExtractedRecipe
): recipe is RecipeDetails => 'id' in recipe;

export const isPartial = (
  recipe: ExtractedRecipe
): recipe is PartialRecipeDetails => !isNotPartial(recipe);

export const isSameImage = (
  recipe: ExtractedRecipe,
  newImage: string
): boolean => getRecipeImageUrl(recipe) === newImage;

export const getUnstructuredParsingUsed = (recipe: ExtractedRecipe) =>
  isPresent(recipe) && isPartial(recipe) && recipe.unstructuredParsingUsed;

export const getFields = (recipe: ExtractedRecipe) => {
  const hasIngredients = recipe.ingredients.length > 0;
  const hasImage = getRecipeImageUrl(recipe) !== undefined;
  const hasDescription =
    recipe.description !== undefined && recipe.description !== '';
  const hasServings = recipe.servings !== undefined;
  const hasPrepTime = recipe.durations.prepTime !== undefined;
  const hasCookTime = recipe.durations.cookTime !== undefined;
  const hasCollections =
    isNotPartial(recipe) && getRecipeCollectionIds(recipe).length > 0;

  const sourceName = getRecipeSourceName(recipe, undefined);
  const hasSourceName = sourceName !== undefined;
  const sourceUrl = getRecipeSourceUrl(recipe);
  const hasUrl = sourceUrl !== undefined;

  return [
    Field.Title,
    hasImage ? Field.Photo : undefined,
    hasIngredients ? Field.Ingredients : undefined,
    hasDescription ? Field.Description : undefined,
    hasServings ? Field.Servings : undefined,
    hasPrepTime ? Field.PrepTime : undefined,
    hasCookTime ? Field.CookTime : undefined,
    hasCollections ? Field.Collection : undefined,
    hasSourceName ? Field.SourceName : undefined,
    hasUrl ? Field.SourceURL : undefined,
  ].filter(isPresent);
};
