import take from 'lodash/take';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { GroupedItem } from 'types/recipes';

import { Container, Item, LeftItems, List, Text } from './markup';
import { Placeholder } from './Placeholder';

const NUMBER_OF_VISIBLE_INGREDIENTS = 3;

interface Props {
  ingredients: GroupedItem[];
  isSaved: boolean;
}

export const Ingredients: React.FC<Props> = ({ ingredients, isSaved }) => {
  const { t } = useTranslation();

  const itemsLeft = ingredients.length - NUMBER_OF_VISIBLE_INGREDIENTS;

  const hasIngredients = ingredients.length !== 0;

  return (
    <Container>
      {hasIngredients ? (
        <>
          <List>
            {take(ingredients, NUMBER_OF_VISIBLE_INGREDIENTS).map(
              (ingredient, index) => (
                <Item key={index}>
                  <Text>{ingredient.text}</Text>
                </Item>
              )
            )}
          </List>
          {itemsLeft > 0 ? (
            <LeftItems>{t('plusMore', { count: itemsLeft })}</LeftItems>
          ) : null}
        </>
      ) : isSaved ? null : (
        <Placeholder />
      )}
    </Container>
  );
};
