import { isPresent } from '@whisklabs/typeguards';
import { action, observable } from 'mobx';

import { getRecipeImageUrl, isSameImage } from 'helpers/recipe';
import { trackChangeImageCancelClicked } from 'mixpanel/trackChangeImageCancelClicked';
import { trackChangeImageClicked } from 'mixpanel/trackChangeImageClicked';
import { trackImageChanged } from 'mixpanel/trackImageChanged';
import { recipeStore } from 'stores';

class PreviewImageStore {
  @observable isChangeImageOpened = false;
  @observable image = isPresent(recipeStore.recipe)
    ? getRecipeImageUrl(recipeStore.recipe)
    : undefined;

  @action private setIsChangeImageOpened = (isOpened: boolean) => {
    this.isChangeImageOpened = isOpened;
  };

  @action private setImage = (image: string) => {
    this.image = image;
    trackImageChanged();
  };

  changeImage = (newImage: string) => {
    this.setIsChangeImageOpened(false);
    if (this.image === newImage) return;

    this.setImage(newImage);

    if (
      recipeStore.isRecipeSaved &&
      isPresent(recipeStore.recipe) &&
      !isSameImage(recipeStore.recipe, newImage)
    ) {
      recipeStore.editRecipe({ image: newImage });
    }
  };

  changeImageClose = () => {
    trackChangeImageCancelClicked();
    this.setIsChangeImageOpened(false);
  };

  changeImageOpen = () => {
    trackChangeImageClicked();
    this.setIsChangeImageOpened(true);
  };
}

export const previewImageStore = new PreviewImageStore();
